}
}
var objlength = Object.keys(this.dataObj).length; 
if (falgnum >= objlength) {

this.$vux.loading.hide(); 
this.popshow = false; 
if (
_this.twoWeather.today.Temperature != "13/25" &&
_this.twoWeather.today.state != "05" &&
Object.keys(_this.currObj).length > 0
) {

sessionStorage.setItem("cacheTime", new Date().getTime());
let objectArr = new Object();
objectArr.currObj = _this.currObj;
objectArr.twoWeather = _this.twoWeather;
sessionStorage.setItem("objectArr", JSON.stringify(objectArr));
}
}
