changeLang(str) {
var returnStr = "";
if (str == "zh-CN") {
returnStr = "繁體中文";
} else if (str == "en-US") {
returnStr = "English";
} else if (str == "pt-PT") {
returnStr = "Português";
}
return returnStr;
