}
var qurl =
_this.requestUrl + "sciencepopularization/sciencepopularizations";
var param = {
keyword: "",
pageno: "1",
pagesize: "10",
qtype: "0",
language: i18nstr
};
_this.$commonAjax(
"POST",
qurl,
param,
function(data) {

_this.dataObj.d_data = true;
_this.verificationShowLoad();

if (data.data != "") {
var datalist = JSON.parse(data.data);
datalist = datalist;
var basList = new Array();
for (var i = 0; i < datalist.length; i++) {

var obj = new Object();
obj.id = datalist[i].id;
obj.url = "javascript";
obj.img = datalist[i].swiperurl;
