moveEnd(e) {
let pageX = e.changedTouches[0].pageX;
console.log(pageX + "===>" + this.pageXI);
/*左右滑*/
if (pageX > this.pageXI + 55) {
this.drawerVisibility = true;
this.swcount = 0;
} else if (pageX + 55 < this.pageXI) {
this.drawerVisibility = false;
this.swcount = 0;
} else {
this.swcount = 0;
}
},
